.friendMessage {
    display: flex;
    flex-direction: column;
}

.friendImageAndText {
    display: flex;

}

.friendImageAndText img {
    width: 50px;
    height: 50px;
    border-radius: 30px;

}

.friendText {
    background-color: #dde5dd;
    border-radius: 10px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
}

.friendText:after {

}

.friendName {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
}

.friendMessageText {
    font-family: Montserrat;
    font-size: 16px;
    font-style: normal;
    line-height: 20px;
}

.friendTime {
    font-family: Montserrat;
    font-size: 10px;
    font-style: normal;
    font-weight: 600;
    line-height: 12px;
}
